  display: none;
  justify-content: center;
  align-items: center;
  position: absolute;
  z-index: 3;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(251, 251, 251, 0.8);
  opacity: 0;
  transition: 0.3s;
}
.loader_active {
  display: flex;
}
.loader_animate {
  opacity: 1;
  visibility: visible !important;
}
.loader_button {
  background: rgba(0, 0, 255, 0.8);
}

.loader_button .loader__content {
  transform: scale(0.7);
}

.loader__spinner {
  animation: rotate 2s linear infinite;
  width: 2.6042vw;
  height: 2.6042vw;
}
@media (max-width: 1280px) {
  .loader__spinner {
    width: 6.5104vw;
    height: 6.5104vw;
  }
}
@media (max-width: 600px) {
  .loader__spinner {
    width: 13.3333vw;
    height: 13.3333vw;
  }
}
@keyframes rotate {
  to {
    transform: rotate(360deg);
  }
}

.loader__path {
  fill: none;
  stroke: blue;
  stroke-linecap: round;
  stroke-width: 4px;
  animation: dash 1.5s ease-in-out infinite;
}
.loader_button .loader__path {
  stroke: white;
  stroke-width: 5px;
}
@keyframes dash {
  0% {
    stroke-dasharray: 1, 150;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -35;
  }
  100% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -124;
  }
}

.form {
  position: relative;
}

.form__error-text {
  display: block;
  font-size: 14px;
  color: red;
  text-align: left;
  touch-action: none;
  pointer-events: none;
}

.breadcrumbs {
  margin-bottom: 14px;
  line-height: 1.6;
}
.breadcrumbs > span {
  display: inline-block;
  vertical-align: middle;
}
.breadcrumbs a {
  position: relative;
  margin-right: 20px;
}
.breadcrumbs a::after {
  content: "/";
  position: absolute;
  bottom: -1px;
  right: -16px;
  color: gray;
  pointer-events: none;
}
.breadcrumbs .breadcrumb_last {
  color: gray;
}

.pagination {
  display: flex;
  justify-content: flex-end;
  margin-top: 40px;
  text-align: center;
}

.pagination__link {
  flex-shrink: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  max-width: calc((100vw - 30px - 24px) / 9);
  max-height: calc((100vw - 30px - 24px) / 9);
  line-height: 44px;
  color: black;
  fill: black;
  text-decoration: none;
  border: none;
  background: white;
  border-radius: 12px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  overflow: hidden;
}
.pagination__link:not(:first-child) {
  margin-left: 4px;
}
.pagination__link[href]:hover {
  background: #dfdfdf;
}
.pagination__link_active, .pagination__link_active:hover {
  color: white;
  background: blue;
  box-shadow: none;
}

.popup {
  display: none;
  position: fixed;
  z-index: 11;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.popup_active {
  display: block;
}
.admin-bar .popup {
  z-index: 99999;
}

.popup__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.75);
  backdrop-filter: blur(6px);
  opacity: 0;
  transition: opacity 0.3s;
}
.popup_animate .popup__bg {
  opacity: 1;
}
.page_mobile .popup__bg {
  backdrop-filter: none;
}

.popup__wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  opacity: 0;
  transition: opacity 0.3s;
}
.popup_animate .popup__wrapper {
  opacity: 1;
}

.popup__container {
  display: table;
  table-layout: fixed;
  width: 100%;
  min-height: 100%;
}

.popup__content {
  display: table-cell;
  vertical-align: middle;
  padding: 5px;
}

.popup__box {
  position: relative;
  padding: 30px;
  width: 100%;
  max-width: 440px;
  margin: auto;
  background: #fbfbfb;
  border-radius: 15px;
  overflow: hidden;
}

.popup__close {
  position: absolute;
  top: 6px;
  right: 6px;
  width: 35px;
  height: 35px;
  background: url("data:image/svg+xml,%3Csvg viewBox='0 0 14 14' width='14' height='14' xmlns='http://www.w3.org/2000/svg' %3E%3Crect transform='rotate(-45)' x='-.83126' y='.83126' width='1.6625' height='18.136'/%3E%3Crect transform='rotate(45)' x='9.0682' y='-9.0682' width='1.6625' height='18.136'/%3E%3C/svg%3E%0A");
  background-repeat: no-repeat;
  background-position: center;
  transition: 0.3s;
}
.popup__close:hover {
  opacity: 0.5;
}

.popup__title {
  margin-bottom: 20px;
  font-weight: 500;
  font-size: 36px;
  line-height: 1.2;
}

.pswp img {
  object-fit: contain;
}

.pswp__img--placeholder {
  display: none !important;
}

.header {
  position: absolute;
  z-index: 11;
  top: 0;
  left: 0;
  right: 0;
  padding: 10px 0;
  transition: 0.3s, margin-right 0s;
}
@media (max-width: 1280px) {
  .header {
    padding: 23px 0;
  }
}
.header_fixed {
  position: fixed;
}
.header_out {
  transform: translate3d(0, calc(-100% - 20px), 0);
}
.admin-bar .header {
  top: 32px;
}
@media (max-width: 782px) {
  .admin-bar .header {
    top: 46px;
  }
}
@media (max-width: 600px) {
  .admin-bar .header {
    position: absolute;
    top: 0;
    transform: none;
  }
}
.header::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: white;
  box-shadow: 0 -5px 25px rgba(0, 0, 0, 0.2);
  transition: 0.3s;
}
@media (min-width: 1281px) {
  .header:not(.header_shadow)::before {
    box-shadow: none;
  }
}
@media (max-width: 1280px) {
  .header::after {
    content: "";
    position: fixed;
    z-index: -2;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: black;
    opacity: 0;
    visibility: hidden;
    transition: 0.3s;
  }
}
@media (max-width: 1280px) {
  .header_active-menu::after {
    opacity: 0.5;
    visibility: visible;
  }
}

.header + * {
  margin-top: 82px;
}
@media (max-width: 1280px) {
  .header + * {
    margin-top: 67px;
  }
}

.header__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (min-width: 1281px) {
  .header__toggle {
    display: none;
  }
}
@media (max-width: 1280px) {
  .header__toggle {
    padding: 8px;
    margin: -8px;
    transition: 0.3s;
  }
}

@media (max-width: 1280px) {
  .header__lines {
    position: relative;
    margin: 6px 0;
  }
}
@media (max-width: 1280px) {
  .header__lines, .header__lines::before, .header__lines::after {
    display: block;
    width: 23px;
    height: 2px;
    background: black;
    border-radius: 1px;
    transition: all 0.3s;
  }
}
@media (max-width: 1280px) {
  .header__lines::before, .header__lines::after {
    content: "";
    position: absolute;
    right: 0;
  }
}
@media (max-width: 1280px) {
  .header__lines::before {
    top: -6px;
  }
}
@media (max-width: 1280px) {
  .header__lines::after {
    bottom: -6px;
    width: 16px;
  }
}
@media (max-width: 1280px) {
  .header_active-menu .header__lines {
    background: rgba(0, 0, 0, 0);
  }
}
@media (max-width: 1280px) {
  .header_active-menu .header__lines::before {
    transform: translate3d(0, 6px, 0) rotate(45deg);
  }
}
@media (max-width: 1280px) {
  .header_active-menu .header__lines::after {
    width: 100%;
    transform: translate3d(0, -6px, 0) rotate(-45deg);
  }
}

@media (max-width: 1280px) {
  .header__menu {
    position: fixed;
    z-index: 2;
    top: 67px;
    left: 0;
    transform: translate3d(-340px, 0, 0);
    width: 84%;
    max-width: 320px;
    height: calc(100% - 67px);
    background: white;
    box-shadow: -5px 25px 25px rgba(0, 0, 0, 0.2);
    overflow: hidden;
    transition: transform 0.3s;
  }
}
@media (max-width: 1280px) {
  .header__menu::before {
    content: "";
    position: absolute;
    z-index: 1;
    bottom: 100%;
    left: 0;
    width: 100%;
    height: 100%;
    box-shadow: 0 -5px 25px rgba(0, 0, 0, 0.2);
  }
}
@media (max-width: 1280px) {
  .header_active-menu .header__menu {
    transform: translate3d(0, 0, 0);
  }
}

@media (min-width: 1281px) {
  .header__menu-list {
    display: flex;
  }
}
@media (max-width: 1280px) {
  .header__menu-list {
    height: 100%;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
  }
}

.footer {
